<script setup lang="ts">
import { ref, onMounted, computed, watch } from 'vue'
import { useConjugationTestStore } from '@/stores/conjugationTest'
const conjugationTestStore = useConjugationTestStore()
import Button from 'primevue/button'
import Checkbox from 'primevue/checkbox'
import { keys } from 'lodash'
// import { useJmdictStore } from '@/stores/jmdict'

const emit = defineEmits(['go'])
onMounted(() => {})

const isBoolean = (item: any) => {
  return 'boolean' === typeof item
}
const mainKeyHalves = computed(() => {
  return [
    keys(conjugationTestStore.formsToTest).slice(0, 8),
    keys(conjugationTestStore.formsToTest).slice(8)
  ]
})
watch(
  () => conjugationTestStore.listsToTest.JMDict,
  (newVal, oldVal) => {
    if (newVal) {
      keys(conjugationTestStore.listsToTest)
        .filter((k: any) => k != 'JMDict')
        .forEach((k2: any) => {
          //@ts-ignore
          keys(conjugationTestStore.listsToTest[k2]).forEach((k3: any) => {
            //@ts-ignore
            conjugationTestStore.listsToTest[k2][k3] = false
          })
        })
    }
  },
  { deep: true }
)
</script>

<template>
  <div class="container">
    <div class="row">
      <div class="col-6">
        <span class="header">Conjugations</span>
      </div>
      <div class="col-3">
        <span class="header">Vocab Type</span>
      </div>
      <div class="col-3">
        <span class="header">List</span>
      </div>
    </div>
    <div class="row">
      <div class="col" v-for="(keyHalf, index) in mainKeyHalves" :key="index">
        <!-- {{ mainKeyHalves }} -->
        <ul>
          <li v-for="key in keyHalf" :key="key">
            <!-- @vue-ignore -->
            <Checkbox
              :binary="true"
              v-if="isBoolean(conjugationTestStore.formsToTest[key])"
              v-model="conjugationTestStore.formsToTest[key]"
            ></Checkbox>
            {{ key }}
            <!-- @vue-ignore -->
            <ul v-if="!isBoolean(conjugationTestStore.formsToTest[key])">
              <!-- @vue-ignore -->
              <li v-for="key2 in keys(conjugationTestStore.formsToTest[key])" :key="key2">
                <!-- @vue-ignore -->
                <Checkbox
                  :binary="true"
                  v-if="isBoolean(conjugationTestStore.formsToTest[key][key2])"
                  v-model="conjugationTestStore.formsToTest[key][key2]"
                ></Checkbox>
                {{ key2 }}
                <!-- @vue-ignore -->
                <ul v-if="!isBoolean(conjugationTestStore.formsToTest[key][key2])">
                  <!-- @vue-ignore -->
                  <li v-for="key3 in keys(conjugationTestStore.formsToTest[key][key2])" :key="key3">
                    <!-- @vue-ignore -->
                    <Checkbox
                      :binary="true"
                      v-if="isBoolean(conjugationTestStore.formsToTest[key][key2][key3])"
                      v-model="conjugationTestStore.formsToTest[key][key2][key3]"
                    ></Checkbox>
                    {{ key3 }}
                  </li>
                </ul>
              </li>
            </ul>
          </li>
        </ul>
      </div>
      <div class="col">
        <ul>
          <li>
            <Checkbox binary v-model="conjugationTestStore.typesToTest['verbs']"></Checkbox
            ><span>Verbs</span>
          </li>
          <li>
            <Checkbox binary v-model="conjugationTestStore.typesToTest['naAdjectives']"></Checkbox
            ><span>Na Adjectives</span>
          </li>
          <li>
            <Checkbox binary v-model="conjugationTestStore.typesToTest['iAdjectives']"></Checkbox
            ><span>I Adjectives</span>
          </li>
        </ul>
        <br /><br />
        <span class="header">Misc</span><br />
        <ul>
          <li>
            <Checkbox binary v-model="conjugationTestStore.showEverything"></Checkbox>Show
            conjugations not being tested
          </li>
        </ul>
      </div>
      <div class="col">
        <ul>
          <li>
            <Checkbox binary v-model="conjugationTestStore.listsToTest.JMDict"></Checkbox>The entire
            JMDict file
          </li>
          <li
            v-for="(key, index) in keys(conjugationTestStore.listsToTest).filter(
              (k: any) => k != 'JMDict'
            )"
            :key="index"
          >
            {{ key }}
            <!-- @vue-ignore -->
            <ul v-if="!isBoolean(conjugationTestStore.formsToTest[key])">
              <!-- @vue-ignore -->
              <li v-for="(key2, index) in keys(conjugationTestStore.listsToTest[key])" :key="index">
                <!-- @vue-ignore -->
                <Checkbox
                  :binary="true"
                  v-model="conjugationTestStore.listsToTest[key][key2]"
                  :disabled="conjugationTestStore.listsToTest.JMDict"
                ></Checkbox
                >{{ key2 }}
              </li>
            </ul>
          </li>
        </ul>
        <!-- <br/><br/>
        Show forms not being tested<Checkbox v-model="conjugationTestStore.showFormsNotTesting"></Checkbox> -->
      </div>
    </div>
    <div class="row">
      <div class="col">
        <Button @click="$emit('go')">Start</Button>
      </div>
    </div>
  </div>
</template>

<style scoped lang="scss">
// .forms {
//   columns: 2;
// }
.header {
  font-weight: bold;
  text-decoration: underline;
}
</style>
