<script setup lang="ts">
import { ref, onMounted, onBeforeMount } from 'vue'
import InputText from 'primevue/inputtext'
import { keys } from 'lodash'
import { bind } from 'wanakana'
import { useConjugationTestStore } from '@/stores/conjugationTest'
const conjugationTestStore = useConjugationTestStore()
import { useJmdictStore } from '@/stores/jmdict'
const jmdictStore = useJmdictStore()

const props = defineProps({
  form: {
    type: String,
    default: ''
  },
  conjugation: {
    type: null,
    default: null
  }
})

const answers = ref({
  // form: props.form
})

const results = ref({})

const checkAnswers = () => {
  keys(props.conjugation).forEach((key: any) => {
    keys(props.conjugation[key]).forEach((key2: any) => {
      //@ts-ignore
      results.value[key][key2] =
        //@ts-ignore
        props.conjugation[key][key2].kanji == answers.value[key][key2] ||
        //@ts-ignore
        props.conjugation[key][key2].kana == answers.value[key][key2]
    })
  })
}

defineExpose({ checkAnswers, reset })

onBeforeMount(() => {
  reset()
})

function reset() {
  keys(props.conjugation).forEach((e: any) => {
    //@ts-ignore
    answers.value[e] = {}
    //@ts-ignore
    results.value[e] = {}
    keys(props.conjugation[e]).forEach((f: any) => {
      //@ts-ignore
      answers.value[e][f] = ''
      //@ts-ignore
      results.value[e][f] = null
    })
  })
}

onMounted(() => {
  document.querySelectorAll('.p-inputtext').forEach((i: any) => {
    bind(i)
  })
})
</script>

<template>
  <div class="container">
    <div class="row">
      <div class="col">
        <span>{{ form }}</span>
      </div>
      <div class="col" v-for="key2 in keys(conjugation[keys(conjugation)[0]])" :key="key2">
        <span>{{ key2 }}</span>
      </div>
    </div>
    <!-- <div class="row">
      <div class="col">
        <span>{{ conjugation }}</span>
      </div>
    </div> -->
    <template v-for="key in keys(conjugation)" :key="key">
      <div class="row">
        <div class="col">
          <span>{{ key }}</span>
        </div>
        <div class="col" v-for="key2 in keys(conjugation[key])" :key="key2">
          <!-- @vue-ignore -->
          <InputText
            v-if="conjugationTestStore.formsToTest[props.form][key][key2] == true"
            :class="
              results[key][key2] == true
                ? 'correct'
                : results[key][key2] == false
                  ? 'incorrect'
                  : ''
            "
            v-model="answers[key][key2]"
          ></InputText>
          <!-- @vue-ignore -->
          <!-- <span
            class="answer"
            v-if="
              conjugationTestStore.formsToTest[props.form][key][key2] == false ||
              results[key][key2] != null
            "
            >{{ conjugation[key][key2].kanji }} ({{ conjugation[key][key2].kana }})</span
          > -->
          <span
            class="answer"
            v-if="conjugationTestStore.showEverything || results[key][key2] != null"
            >{{ conjugation[key][key2].kanji }} ({{ conjugation[key][key2].kana }})</span
          >
        </div>
      </div>
    </template>
    <!-- <div class="row">
      <div class="col">answers.value {{ answers }}</div>
    </div> -->
  </div>
</template>

<style scoped lang="scss">
.col {
  padding-top: 5px;
  padding-bottom: 5px;
}
.correct {
  border-color: #008000;
  background-color: #00800030;
}
.incorrect {
  border-color: #ff0000;
  background-color: #ff000030;
}
.answer {
  color: #008000;
  font-weight: bold;
}
.correct,
.incorrect {
  border-width: 3px;
}
.p-inputtext {
  display: block;
  ::after {
    content: '\a';
    white-space: pre;
  }
}
</style>
