<script setup lang="ts">
import { ref, onMounted, inject, onUpdated } from 'vue'
import { useJmdictStore } from '@/stores/jmdict'
import type { Word } from '@/interfaces/jmdict'
// import type { iConjugationObject } from '@/interfaces/conjugations'
import { conjugate } from '@/scripts/conjugate'
const jmdictStore = useJmdictStore()
import Info from '@/components/ConjugationTest/Info.vue'
import TestItem from '@/components/ConjugationTest/TestItem.vue'
import { keys, values } from 'lodash'
import Button from 'primevue/button'
import Setup from '@/components/ConjugationTest/Setup.vue'
// import { useConjugationTestStore } from '@/stores/conjugationTest'
// const conjugationTestStore = useConjugationTestStore()
import BlockUI from 'primevue/blockui'
import { useConjugationTestStore } from '@/stores/conjugationTest'
const conjugationTestStore = useConjugationTestStore()

onMounted(() => {
  // getItem(true, true, true)
})
onUpdated(() => {})
const notify = inject('notify')
const toTest = ref<null | Word | undefined>(null)
const conjugations = ref<null | undefined | any>(null)
const getItem = () => {
  toTest.value = jmdictStore.getRandomItemToConjugate()
  if (toTest.value == undefined) {
    //@ts-ignore
    notify.error('No item found to test')
    return null
  } else if (jmdictStore.isWordVerb(parseInt(toTest.value.id))) {
    conjugations.value = conjugate.verb({
      verb:
        //@ts-ignore
        toTest.value[jmdictStore.uglyJmdict['kanji']].length > 0
          ? //@ts-ignore
            toTest.value[jmdictStore.uglyJmdict['kanji']][0][jmdictStore.uglyJmdict['text']]
          : //@ts-ignore
            toTest.value[jmdictStore.uglyJmdict['kana']][0][jmdictStore.uglyJmdict['text']],
      //@ts-ignore
      reading: toTest.value[jmdictStore.uglyJmdict['kana']][0][jmdictStore.uglyJmdict['text']]
    })
  } else if (jmdictStore.isWordNaAdjective(parseInt(toTest.value.id))) {
    conjugations.value = conjugate.na_adjective({
      word:
        //@ts-ignore
        toTest.value[jmdictStore.uglyJmdict['kanji']].length > 0
          ? //@ts-ignore
            toTest.value[jmdictStore.uglyJmdict['kanji']][0][jmdictStore.uglyJmdict['text']]
          : //@ts-ignore
            toTest.value[jmdictStore.uglyJmdict['kana']][0][jmdictStore.uglyJmdict['text']],
      //@ts-ignore
      reading: toTest.value[jmdictStore.uglyJmdict['kana']][0][jmdictStore.uglyJmdict['text']]
    })
  } else if (jmdictStore.isWordIAdjective(parseInt(toTest.value.id))) {
    conjugations.value = conjugate.i_adjective({
      word:
        //@ts-ignore
        toTest.value[jmdictStore.uglyJmdict['kanji']].length > 0
          ? //@ts-ignore
            toTest.value[jmdictStore.uglyJmdict['kanji']][0][jmdictStore.uglyJmdict['text']]
          : //@ts-ignore
            toTest.value[jmdictStore.uglyJmdict['kana']][0][jmdictStore.uglyJmdict['text']],
      //@ts-ignore
      reading: toTest.value[jmdictStore.uglyJmdict['kana']][0][jmdictStore.uglyJmdict['text']]
    })
  }
  submitted.value = false
}

const testItems = ref([])
const submitted = ref(false)

const submit = () => {
  testItems.value.forEach((t: any) => {
    t.checkAnswers()
  })
  submitted.value = true
}
const next = () => {
  testItems.value.forEach((t: any) => {
    t.reset()
  })
  getItem()
}
const hasTestItem = (conj: any) => {
  let has = false
  let v = values(conj)
  v.forEach((e: any) => {
    if (typeof e === 'boolean') {
      has = e || has
    } else if (typeof e === 'object') {
      values(e).forEach((f: any) => {
        has = f || has
      })
    }
  })
  return has
}
</script>

<template>
  <BlockUI :blocked="jmdictStore.loading">
    <div class="container">
      <div class="row">
        <div class="col">Conjugation Test</div>
      </div>
      <!-- <div class="row">
      <div class="col">
        {{ jmdictStore.verbIds.length }} {{ jmdictStore.naAdjectiveIds.length }}
        {{ jmdictStore.iAdjectiveIds.length }}
      </div>
    </div> -->
      <template v-if="toTest == null">
        <Setup @go="getItem"></Setup>
      </template>
      <template v-else>
        <div class="row">
          <div class="col">
            <Info :word="toTest"></Info>
          </div>
        </div>
        <!-- <div class="row">
        <div class="col">
          {{ conjugations }}
        </div>
      </div> -->
        <!-- <div class="row mainConjRow"> -->
        <template
          v-for="(conj, index) in keys(conjugations).filter((k: any) => k.indexOf('stem') == -1)"
          :key="index"
        >
          <!-- @vue-ignore -->
          <div
            v-if="
              hasTestItem(conjugationTestStore.formsToTest[conj]) ||
              conjugationTestStore.showEverything
            "
            :id="'row' + index"
            :class="['row', 'mainConjRow']"
          >
            <!-- @vue-ignore -->
            <!-- {{ conjugationTestStore.formsToTest[conj] }} -->
            <div class="col">
              <TestItem :form="conj" :conjugation="conjugations[conj]" ref="testItems"></TestItem>
            </div></div
        ></template>
        <!-- </div> -->
        <div class="row">
          <Button @click="submit" v-if="!submitted">Submit</Button>
          <Button @click="next" v-else>next</Button>
        </div>
      </template>
    </div>
  </BlockUI>
</template>

<style scoped lang="scss"></style>
